    <h1 class="mt-4 mb-3">monager:
        <small>How it works</small>
    </h1>
        <p>
        Pooling money (Committee) is a saving and borrowing system which is very popular in almost every part of India.<br>
        It is some sort of money raising scheme. Each Pooling scheme or Pool is created and manged by few  trusted individuals or so called managers.<br>
        This practice of collecting money from people and keeping/managing it without accounting for any recorded transactions is illegal.<br>

        Blockchain provides a best transperent and trustless method for creating online Pools.<br>
        <img src="./../assets/poolingScheme1.png" width="800" height="450" >

        <h3>How it works?</h3>
        People form a group and start pooling up a specific amount of money. Every month, one member can pick the total pooled amount by the process of bidding. 
        Lets take an example to understand better, there is a group of 10 people contributing Rs. 5000 each every month. 
        This committee will pool up Rs. 50,000 (Rs. 50,000 x 10 members) every month. One of the 10 members can pick this Rs. 50,000 amount by bidding the highest amount (reverse auctioning).<br>

        Lets say, Mr. A and Mr. B are willing to pick this Rs. 50,000 amount this month. Mr. A bids Rs. 4,000 and Mr. B bids Rs. 4,500. 
        This means that Mr. A is willing to pay Rs. 4,000 as interest and Mr. B is agreeing to pay Rs. 4,500 as interest, in order to get Rs. 50,000. In this case, 
        Mr. B will win the bid and he will get a net-net of Rs. 45,500 (Rs. 50,000-Rs. 4,500) in hand.<br>

        The bid amount of Mr. B i.e. Rs. 4,500 will be equally distributed among his remaining 9 committee members. Each one will get Rs. 500 as the returns on their investment.<br>

        This process goes on for 10 months (as there are 10 members in this committee) and every month there will be a unique winner who can pick the lump-sum.<br>

        Offline committees are illegal as these are against the Chit funds Act 1982. In offline committees, 
        there is a committee who collects money from the committee members and keeps it with himself and gives it to the winner subtracting his commission which is very high. 
        This practice of collecting money from people and keeping/managing it without accounting for any recorded transactions is illegal.
    </p>

