<template>
    <div class="newWallet">
        <div class="float-right">
            <ol class="breadcrumb ">
                <li class="breadcrumb-item">
                    <a href="/">Home</a>
                </li>
                <li class="breadcrumb-item active">Active Pools</li>
            </ol>
        </div><br><br>
        <div class="container">
            <b-alert class="mt-1 mb-2" variant="secondary" show>
                <b-row>
                    <b-col sm="6">
                <h5 ><span class="border-bottom border-primary">Active Pools</span></h5>
                </b-col sm="6">
                    <b-col>
                <b-button @click="callRestService" variant="secondary" size="sm" class="float-right">Reload</b-button>
                </b-col>
                </b-row>
            </b-alert>
            <b-alert variant="info" dismissible>{{msg}}(Location: {{location}}). Click to close over there <b>&rArr;</b> </b-alert>
            <b-alert variant="success" :show="showBackendResponse">Backend response:{{response}}</b-alert>
            <b-alert variant="danger" :show="showBackendError" dismissible>
                <h4 class="alert-heading">{{errors.header}}:</h4>
                <p>{{errors.msg}}</p><hr>   
                <p class="mb-0">{{errors.time}}</p>
            </b-alert>
            <!--<b-alert variant="danger" :show="showBackendErrorHeader">Backend response Error:{{errors.header.data}}</b-alert>-->
            <!-- Main table element -->
            <b-table striped show-empty stacked="md" hover small head-variant="dark" :items="items" :fields="fields">
                <!--
                <template slot="ownerAddress" slot-scope="data">
                    <a :href="`#${data.value.replace(/[^a-z]+/i,'-').toLowerCase()}`">
                    {{data.value}}
                    </a>
                -->
                <template slot="ownerAddress" slot-scope="row">
                    <!--<a @click="loadTran(row.value,row.item, row.index, $event.target)" :href="'#'">-->
                    <a @click="loadTran(row.value,row.index,row.item)" :href="'#'">
                    {{row.value}}
                    </a>
                </template>
            </b-table>
           <!-- <button @click="callRestService">Call Api </button>-->
        </div>
    </div>
</template>

<script>
	// import axios from 'axios'
	import {AXIOS} from './http-common.js'
        import {MYWEB3} from './eth-web3.js'
	export default {
	  name: 'NewWallet',
	  data () {
	    return {
	      msg: 'Welcome to NewWallet',
              dismissSecs: 5,
              dismissCountDown: 0,
	      showBackendResponse: false,
	      showBackendError: false,
	      showBackendErrorHeader: false,
	      location: 'D:\\WorkingFolder\\workspace\\monager\\frontend\\src\\components\\NewWallet.vue',
	      response:[] ,
              errors: [
          	{header:''},
          	{msg: ''},
                {time: ''}
         	],
              fields: [
                {key: 'name',sortable: true},
                {key: 'address',sortable: true},
                {key: 'balance'},
                {key: 'ownerAddress'},
                {key: 'isActive',sortable: true}
              ],
              items: [ ]
	    }
	  },
            methods:{
	  	callRestService : function(){
	  		this.showBackendError= false;
                        AXIOS.get('/getAllPools')
                        .then(response=>{
	  			this.response=response.data
	  			//this.showBackendResponse= true;
                                this.items=response.data
                                //this.items.push(data);
                                var i;
                                var activestatus;
                                for(i=0;i<(this.items.length);i++){
                                    activestatus=(this.items[i].isActive)?'success':'danger'
                                    //console.log((this.items[i].name).replace('E','9'));
                                    //this.$set(this.items[i],'_rowVariant' , activestatus)
                                    this.$set(this.items[i],'balance' ,'Click Addr to load balance')
                                    this.$set(this.items[i],'_cellVariants' , {'isActive':activestatus})
                                    
                                }
	  		})
	  		.catch(e=>{
	  			//console.log(e.response)
	  			//this.errors.detail=e;
	  			//this.errors.header=e.response;
	  			this.showBackendError= true;
	  			this.showBackendErrorHeader= true;
                                this.errors.pop();
                                this.errors.msg=e.response.data;
                                this.errors.header=e.response.status+' '+e.response.statusText;
                                this.errors.time=e.response.status+' '+e.response.headers.date;
	  			//this.errors.push(e)
	  			
	  		})
	  	},
                loadTran:function(a,b,c,d){
                    //console.log("Row Index"+b);
                    //console.log("Row Item"+c);
                    try{
                        var bal=MYWEB3.eth.getBalance(a)
                        this.$set(this.items[b],'balance' ,bal+'')
                    }catch(err){
                        //console.log(err);
                        this.errors.header=err.name;
                        this.errors.msg=err.message;
                        this.errors.time=new Date();
                        this.showBackendError= true;
                    }
                }
            },
            mounted:function(){
                this.callRestService()
            }
	}	
</script>


