<template>
    <div id="app">
        <div>
            <div>
                <b-navbar toggleable="md" type="dark" variant="dark" fixed="top" topmargin="big">
                    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
                    <b-navbar-brand href="/" type="dark"> 
                        <span class="border-bottom border-primary" >{{appName}}</span>
                        <!--<img src="./assets/monagerLogo1.png" alt="monager">-->
                    </b-navbar-brand>
                    <b-collapse is-nav id="nav_collapse">
                        <b-navbar-nav class="ml-auto">
                            <b-nav-item v-for="option in options"  v-bind:href="option.href" v-bind:key="option.text">{{option.text}}</b-nav-item>
                        </b-navbar-nav>
                        <!--
                        <b-nav-item-dropdown text="Start" right>
                            <b-dropdown-item v-for="option in options" v-bind:href="option.href" v-bind:key="option.text" >{{option.text}}</b-dropdown-item>
                        </b-nav-item-dropdown>
                        -->
                    </b-collapse>
                </b-navbar>
            </div>    
        </div>
        <router-view></router-view>
        <div>
            <b-navbar  type="dark" variant="dark" fixed="bottom" >
                <b-navbar-brand href="/" type="light"> 
                    <img src="./assets/monagerLogo1.png" alt="monager" height="42">
                </b-navbar-brand>
                <!--
                <b-navbar-brand href="https://www.ethereum.org/" type="light">
                    <img src="./assets/eth.png" alt="monager" height="42">
                </b-navbar-brand>                
                -->
                <b-navbar-nav >
                    <p class="m-0 text-white">Copyright &copy; Your Website 2018</p>
                </b-navbar-nav>                
            </b-navbar>
        </div>
    </div>
</template>
<script>
export default {
  name: 'App',
  data: function (){
    console.log("reached App Page DOM");
    return {
      appName: 'monager',
      options: [
      {
        href: '/about',
        text: 'About'
      },
      {
        href: "/contact",
        text: 'Contact'
      },
      {
        // href: '/#/newWallet', use this method when not using mode: 'history' in index.js is not used
        href: "/activePools",
        text: 'ActivePools'
      },
      {
        href: '/createNew',
        text: 'CreatePool'
      },
      {
        href: '/login',
        text: 'Login'
      }
      ]
    }
  }
}
</script><!--
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
